// 
@keyframes load{0%{stroke-dashoffset:0}}
.progressSec {
    background-color: transparent;
    position: relative;
    display: inline-block;
    padding: 0;
    text-align: center;
    li {
        display: inline-block;
        position: relative;
        text-align: center;
        font-family: Lato;
        font-weight:100;
        margin: 2rem;
        font-family: 'IRANSans';
        &::before {
            content: attr(data-name);
            position: absolute;
            width: 100%;
            bottom: -2rem;
            font-weight:400;
        }
        &::after {
            content: attr(data-percent);
            position: absolute;
            width: 100%;
            top: 3.7rem;
            left: 0;
            font-size: 2rem;
            text-align: center;
        }
    }
    svg {
        width: 10rem;
        height: 10rem;
        &:nth-child(2) {
            position: absolute;
            left: 0;
            top: 0;
            transform: rotate(-90deg);
        }
        &:nth-child(2) path {
            fill: none;
            stroke-width: 4;
            stroke-dasharray: 629;
            stroke: #ffffffaa;
            opacity:.9;
            animation: load 10s;
        }
    }
}
